.arrivals__items { font-size: 0; }
.arrivals__title { margin-bottom: 20px; font-size: 20px; font-weight: bold; text-transform: uppercase; color: #000; letter-spacing: 8px; }
.arrivals__btn { display: inline-block; height: 33px; width: 190px; margin-bottom: 50px; padding-top: 3px; line-height: 30px; font-size: 16px; font-weight: bold; text-align: center; letter-spacing: 2px; text-transform: uppercase; background: url(/img/rect-green.png) no-repeat; cursor: pointer; }
.arrivals__item { width: 23%; margin: 0 1%; margin-bottom: 50px; display: inline-block; vertical-align: top; }
@media only screen and (max-width: 980px) { .arrivals__item { width: 48%; } }
@media only screen and (max-width: 480px) { .arrivals__item { width: 50%; } }
.arrivals__item:hover { opacity: 0.7; }
.arrivals__item img { margin-bottom: 30px; }
@media only screen and (max-width: 480px) { .arrivals__item img { height: 300px; } }
.arrivals__item-name { margin-bottom: 25px; font-size: 15px; font-weight: bold; line-height: 16px; text-transform: uppercase; letter-spacing: 3px; }
.arrivals__item .btn_buy { background: #fe7d91; }
.arrivals__row { width: 50%; display: inline-block; vertical-align: top; }
@media only screen and (max-width: 980px) { .arrivals__row { width: 100%; } }

.hash-tag-container { position: relative; margin-bottom: 60px; text-align: center; }
@media only screen and (max-width: 480px) { .hash-tag-container { margin-bottom: 110px; } }

.hash-tag { font-size: 45px; color: #000; }
@media only screen and (max-width: 480px) { .hash-tag { font-size: 35px; } }
.hash-tag p { font-size: 12px; font-weight: bold; text-transform: uppercase; letter-spacing: 1px; }

.inst__btn { position: absolute; top: 20px; right: 60px; }
@media only screen and (max-width: 650px) { .inst__btn { right: 5%; } }
@media only screen and (max-width: 480px) { .inst__btn { top: 80px; right: 50%; margin-right: -33px; } }
.inst__btn .icon-instagram { width: 65px; height: 65px; background: url(/img/instagram.svg) no-repeat; cursor: pointer; }

.gallery { margin-bottom: 100px; font-size: 0; text-align: center; }
@media only screen and (max-width: 980px) { .gallery { margin-bottom: 20px; } }
.gallery__wrap { margin-right: -1%; }
.gallery__item { width: 19%; margin-right: 1%; margin-bottom: 10px; display: inline-block; vertical-align: top; transition: 0.2s all linear; background-repeat: no-repeat; background-size: cover; }
@media only screen and (max-width: 980px) { .gallery__item { width: 280px; } }
.gallery__item:before { content: ""; display: inline-block; padding-bottom: 100%; }
.gallery__item:hover { opacity: 0.7; }
.gallery__item:last-child { margin-right: 0; margin-bottom: 0; }

.footer { padding-bottom: 60px; }

.footer__nav { margin-bottom: 20px; }
.footer__nav .nav { font-size: 0; text-align: center; }
.footer__nav .nav__item { position: relative; display: inline-block; vertical-align: top; margin-right: 30px; }
@media only screen and (max-width: 480px) { .footer__nav .nav__item { display: block; margin-right: 0; margin-bottom: 20px; } }
.footer__nav .nav__item:after { content: ""; position: absolute; top: 3px; right: -17px; width: 5px; height: 5px; border-radius: 50%; background: #4d4d4d; }
@media only screen and (max-width: 480px) { .footer__nav .nav__item:after { top: 21px; right: 50%; margin-right: -2px; } }
.footer__nav .nav__item:last-child:after { content: none; }
.footer__nav .nav__link { font-size: 13px; line-height: 13px; color: #4d4d4d; text-transform: uppercase; text-decoration: none; letter-spacing: 2px; }

.footer__content { margin-bottom: 15px; padding: 42px 0; background: #fee7ea; text-align: center; }
@media only screen and (max-width: 980px) { .footer__content { padding: 20px 0; } }

.footer__part { width: 33%; display: inline-block; vertical-align: top; }
@media only screen and (max-width: 980px) { .footer__part { width: 60%; margin: 0 auto; display: block; } }
@media only screen and (max-width: 480px) { .footer__part { width: 90%; margin: 0 auto; } }
.footer__part_left { padding: 5px 0; }
@media only screen and (max-width: 980px) { .footer__part_left { padding-bottom: 25px; } }
.footer__part_left a { height: 137px; display: block; background-repeat: no-repeat; background-position: 50% 50%; -webkit-background-size: contain; -o-background-size: contain; background-size: contain; }
.footer__part_right { padding: 20px 0; }
@media only screen and (max-width: 980px) { .footer__part_right { padding: 0; padding-top: 20px; } }
.footer__part_center { padding: 45px 0; border-left: 1px solid #979797; border-right: 1px solid #979797; }
@media only screen and (max-width: 980px) { .footer__part_center { border: none; border-top: 1px solid #979797; border-bottom: 1px solid #979797; } }
@media only screen and (max-width: 480px) { .footer__part_center { padding: 35px 0; border: none; border-top: 1px solid #979797; border-bottom: 1px solid #979797; } }
.footer__part-text { margin-bottom: 10px; font-size: 13px; line-height: 20px; letter-spacing: 3px; }
.footer__part-email { font-size: 20px; line-height: 20px; color: #fe7d91; text-transform: uppercase; text-decoration: none; letter-spacing: 4px; }
@media only screen and (max-width: 1150px) { .footer__part-email { font-size: 15px; } }
@media only screen and (max-width: 480px) { .footer__part-email { font-size: 13px; } }

.footer__in-la { display: inline-block; vertical-align: top; margin-top: 15px; margin-right: 70px; }
@media only screen and (max-width: 480px) { .footer__in-la { margin-right: 50px; } }
.footer__in-la:hover { opacity: 0.7; }
.footer__in-la .icon-love { width: 58px; height: 93px; background: url("/img/in-love.svg") no-repeat; }

.footer__socials { display: inline-block; vertical-align: top; text-align: left; }

.footer__social { position: relative; margin-bottom: 20px; padding-left: 40px; display: block; line-height: 25px; color: #4d4d4d; text-transform: uppercase; text-decoration: none; letter-spacing: 3px; }
.footer__social:last-child { margin-bottom: 0; }
.footer__social:hover { opacity: 0.7; }

.icon-footer-in, .icon-footer-tw, .icon-footer-fb { position: absolute; top: 0; left: 5px; }

.icon-footer-in { background-image: url("../img/icons.png"); width: 25px; height: 25px; background-position: -29px 0px; }

.icon-footer-tw { background-image: url("../img/icons.png"); width: 25px; height: 25px; background-position: 0px -29px; }

.icon-footer-fb { background-image: url("../img/icons.png"); width: 25px; height: 25px; background-position: 0px 0px; }

.footer__copyright { text-align: center; }
.footer__copyright p { display: inline-block; margin-right: 10px; text-transform: uppercase; letter-spacing: 1px; }
@media only screen and (max-width: 480px) { .footer__copyright p { display: block; margin-bottom: 10px; } }
.footer__copyright a { text-transform: uppercase; letter-spacing: 1px; text-decoration: none; }
.footer__copyright a:hover { text-decoration: underline; }


